@import "src/ui/06_common/styles/colors/colors";

.default {
    min-width: 85px;
    margin-top: 10px;
    padding: 10px;
    border-radius:  30px;
    outline: none;
    border: 1px solid $light-theme-bg;
    background-color: $dark-theme-bg;
    color: $light-theme-bg;
    cursor: pointer;
    transition: 0.3s all;
}

.default:hover {
    color: $accent-color;
    border: 1px solid $accent-color;
    cursor: pointer;
}

.default:active {
    opacity: 1;
    color: $accent-color;
    border: 1px solid $accent-color;
}

.red {
    margin-top: 20px;
    margin-bottom: 20px;
    color: $dark-theme-bg;
    background: $accent-color;
    padding: 10px;
    border-radius: 10px;
}

